"use client";

import { useAccount } from "wagmi";
import NotSignedIn from "@/app/components/wallet-sign-in";
import { NavigationHeader } from "@/app/components/NavBar";
import  Main from "@/app/components/main";

export default function HomePage() {
  const { status } = useAccount();

  return (
    <div>
      <NavigationHeader />
      {status === "connected" ? (
        <div className="flex">
          <Main />
        </div>
      ) : (
        <NotSignedIn />
      )}
    </div>
  );
}